body {
    margin: 0;
}



#bar {
    width: 100vw;
    height: 50px;
    position: fixed;
    top: 0;
    z-index: 1;

    display: flex;
    justify-content: space-around;
    align-items: center;

    background-color: rgb(0, 0, 0, 0.1);
    -webkit-backdrop-filter: saturate(180%) blur(20px);
            backdrop-filter: saturate(180%) blur(20px);

}


/* 每个导航栏按钮 */
.barLink {
    font-size: 22px;
    font-weight: 700;
    color: white;

    text-decoration: none;
}

@media screen and (max-width: 500px) {
    #bar {
        height:36px;
    }
    .barLink {
        font-size: 16px;
    }
}
#basic {
    width: 100vw;
    height: 100vh;

    
    background-position: center;

    display: flex;
    justify-content: center;
    align-items: center;
}

@media screen and (max-width:1500px) {
    #basic {
        background-size: cover;
    }
}

/* .Basic-dark {
    background: linear-gradient( rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.5) );
    display: none;

} */
.Basic-light {
    -webkit-filter: brightness(100%);
            filter: brightness(100%);
    transition: all 1s ease 0s;
}

.Basic-dark {
    -webkit-filter: brightness(50%);
            filter: brightness(50%);
    transition: all 1s ease 0s;
}
/* 基本介绍，在中间部分的 */
#basic-ticket {
    display: flex;
    flex-direction: row;

    color: white;
}

@media screen and (max-width:800px ) {
    #basic-ticket {
        /* justify-content:center; */
        align-items: center;
        flex-direction:column;
    }
}

#head-portrait {
    height: 250px;
    width: 250px;
    /* background-image: url(./HPImage/Eric.jpg); */
    background-position: center;
    background-size: 375px;
    border-radius: 50%;
    background-repeat: no-repeat;
}

@media screen and (max-width:800px ) {
    #head-portrait {
        height: 170px;
        width: 170px;
        background-size: 280px;
    }
}


/* height: 100px;
    width: 100px;
    background-size: 200px; */
#information {
    width: 520px;

    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;

    margin: 0 50px;
}

#border {
    width: 300px;

    border: 1px solid white;
}

@media screen and (max-width:800px ) {
    #information {
        align-items: center;
    }
}

@media screen and (max-width:600px ) {
    #information {
        width: 340px;
        margin: 0px;
}
}

@media screen and (max-width: 400px) {
    #border {
        width: 200px;
    }
    #information {
        width: 240px;
    }
}
h1 {
    font-weight: 800;
}

@media screen and (max-width:500px ) {
    h1 {
        font-size: 14px;
    }
}


.contact-mode {
    margin: 10px;
}

.contact-mode > a{
    text-decoration: none;
    color: #ffffff;
    font-size: 16px;
    font-weight: 500;
    padding: 12px 22px;
    border: 2px solid #fff;
    margin-right: 11px;
    margin-left: 11px;
}

.contact-mode > a:hover {
    letter-spacing: 1.5px;
    background-color: #8B2252;
}
.skills {
    width: 100vw;

    margin: 20px 0px 50px;
}


.skill-tickets {
    width: 100%;

    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-row-gap: 20px;
    grid-column-gap: 20px;
    justify-items: center;

}

.skills-title {
    margin: 16px 23px;
}

@media screen and (max-width:900px) {
    .skill-tickets {
        grid-template-columns: repeat(2, 1fr);
    }
}

@media screen and (max-width:740px) {
    .skill-tickets {
        grid-template-columns: repeat(1, 1fr);
}
}
.skill {
    border: 1px solid #dee2e6!important;
    font-family: "Rubik", sans-serif;
    width: 270px;
    border-radius: 5px;
    padding: 2px 25px 10px;
}
.skill-title {
    display: flex;
    justify-content: space-between;
}

@media screen and (max-width:1120px) {
    .skill {
        width: 200px;
    }
}

@media screen and (max-width:900px) {
    .skill {
        width: 270px;
    }
}

@media screen and (max-width:740px) {
    .skill {
        width: 300px;
    }
}
@media screen and (max-width:740px) {
    .skill {
        width: 220px;
    }
}

.progress-bar {
    width: 100%;

    display: flex;
    flex-direction: column;
    justify-content: center;
    overflow: hidden;
    color: #fff;
    text-align: center;
    white-space: nowrap;
    background-color: #007bff;
    transition: width .6s ease;

    background-color: #20c997;

}

.progress {
    display: flex;
    height: 1rem;
    overflow: hidden;
    line-height: 0;
    font-size: .75rem;
    background-color: #e9ecef;
    border-radius: .25rem
}

@media (prefers-reduced-motion:reduce) {
    .progress-bar {
        transition: none
    }
}

.progress-sm {
	height: 0.5rem !important;
}
.footer {
    width: 100vw;
    height: 70px;

    background-color: rgb(288, 4, 81);

    display: flex;
    justify-content: center;
    align-items: center;
}
.footer > p {
    margin: 0px;
}
@media screen and (max-width: 600px) {
    
    .footer {
        display: flex;
        justify-content: center;
        flex-direction: column;
        align-items: center;
    }
}
.aboutMe {
    width: 100vw;
    min-height: 100vh;
    display:flex;
    justify-content: space-around;
    align-items: center;
    flex-direction:column;
    

    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;

    color:wheat;
}

.about-title {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}
.about-title > h1 {
    margin: 0;
}
.line {
    border-top: 1px solid wheat;
    width: 75px;
}

.page-logo {
    display:flex;
    justify-content:center;
    align-items: center;
    flex-direction: row;
}

.logoImg {
    background-position: center;
    background-repeat: no-repeat;
    background-size:100%;
    width: 40px;
    height: 40px;
    margin: 0 10px;
}
.displayWall {
    width: 100vw;
    min-height: 100vh;

    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;

    padding-top: 30px;
    padding-bottom: 20px;
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
}
.workLink {
    width: 100%;
    height: 100%;
    text-decoration:none;
}

.workLink > div{
    width: 100%;
    height: 100%;
}
.workLink > div div {
    transform: translateY(40px);
    transition: all 1s;

}

.workLink:hover > div:hover div {
    transform: translateY(0px);
}

.contents {
    /* border: 1px solid #919866; */
    text-align: center;
    display: inline-block;

    width: 90%;
    margin-bottom: 20px;

    width: 100%;
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-row-gap: 20px;
    grid-column-gap: 20px;
    justify-items: center;
}

.work{
    border: 1px solid #919866;
    text-align: center;
    

    width: 80%;
    height: 200px;
    margin-bottom: 20px;
    width: 300px;

    
}
.contents > div {
    transition: all 1s;

    border-radius: 15px;
    overflow: hidden;
}
.contents > div:hover {
    box-shadow: 0px 0px 40px rgb(12 12 12 / 40%);
    transform: scale(1.06, 1.06);
}

@media screen and (max-width: 1050px) {
    .work {
        width: 200px;
        height: 133.333333333px;
    }
}

@media screen and (max-width: 900px) {
    .contents {
        grid-template-columns: repeat(2, 1fr);

    }
}
@media screen and (max-width: 500px) {
    .work {
        width: 130px;
        height: 87px;
    }
    .contents {
        grid-row-gap: 0px;
        grid-column-gap: 0px;
    }
}
#porfolioPage {
    min-height: 100vh;
    /* background-image: url(/image/GreenBGIEM.jpeg); */
    color: wheat;
}
.contactPage {
    width: 100vw;
    min-height: 100vh;
    display:flex;
    justify-content:center;
    align-items: center;

    background-position: center;
    background-repeat: no-repeat;
}
.error {
    width: 100vw;
    min-height: 100vh;
    display:flex;
    justify-content:center;
    align-items: center;

    background-position: center;
    background-repeat: no-repeat;
}
.theme-btn {
    position: fixed;
    top: 70px;
    right: 30px;

    width: 35px;
    height: 35px;

    background-size: 100%;
    background-position: center;

    z-index: 1;
}

